<template>
  <div id="app">
    <!-- 导航栏 -->
    <nav class="navbar navbar-expand-lg navbar-dark bg-primary fixed-top">
      <div class="container">
        <a class="navbar-brand" href="#home">AI个人网站</a>
        <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
          <span class="navbar-toggler-icon"></span>
        </button>
        <div class="collapse navbar-collapse" id="navbarNav">
          <ul class="navbar-nav ml-auto">
            <li class="nav-item">
              <a class="nav-link" href="#home">首页</a>
            </li>
            <li class="nav-item">
              <a class="nav-link" href="#about">关于AI</a>
            </li>
            <li class="nav-item">
              <a class="nav-link" href="#portfolio">作品展示</a>
            </li>
            <li class="nav-item">
              <a class="nav-link" href="#ai-chat">AI对话</a>
            </li>
          </ul>
        </div>
      </div>
    </nav>

    <!-- Banner图 -->
    <header id="home" class="banner">
      <div class="container h-100">
        <div class="row h-100 align-items-center">
          <div class="col-12 text-center">
            <h1 class="font-weight-bold">欢迎来到我的AI个人网站</h1>
            <p class="lead">这里是我展示AI项目和分享AI见解的地方</p>
            <a href="#about" class="btn btn-primary btn-lg">了解更多</a>
          </div>
        </div>
      </div>
    </header>

    <!-- AI简介 -->
    <section id="about" class="py-5">
      <div class="container">
        <h2 class="text-center mb-4">关于AI</h2>
        <div class="row">
          <div class="col-lg-8 mx-auto">
            <p class="lead">人工智能（AI）是计算机科学的一个分支，致力于创造智能机器。它涵盖了机器学习、深度学习、自然语言处理等多个领域，正在改变我们的生活和工作方式。</p>
          </div>
        </div>
      </div>
    </section>

    <!-- 作品展示 -->
    <section id="portfolio" class="py-5 bg-light">
      <div class="container">
        <h2 class="text-center mb-4">AI项目展示</h2>
        <div class="row">
          <div class="col-md-4 mb-4" v-for="project in aiProjects" :key="project.id">
            <div class="card h-100">
              <img :src="project.image" class="card-img-top" :alt="project.title">
              <div class="card-body">
                <h5 class="card-title">{{ project.title }}</h5>
                <p class="card-text">{{ project.description }}</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>

    <!-- AI对话框 -->
    <section id="ai-chat" class="py-5">
      <div class="container">
        <h2 class="text-center mb-4">与AI对话</h2>
        <div class="row">
          <div class="col-lg-8 mx-auto">
            <div class="card">
              <div class="card-body">
                <div class="chat-messages mb-3" ref="chatMessages">
                  <div v-for="(message, index) in chatMessages" :key="index" :class="['message', message.sender]">
                    {{ message.text }}
                  </div>
                </div>
                <div class="input-group">
                  <input type="text" class="form-control" placeholder="输入您的问题..." v-model="userInput" @keyup.enter="sendMessage">
                  <div class="input-group-append">
                    <button class="btn btn-primary" type="button" @click="sendMessage">发送</button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
export default {
  name: 'App',
  data() {
    return {
      aiProjects: [
        {
          id: 1,
          title: '自然语言处理聊天机器人',
          description: '基于最新的NLP技术开发的智能聊天机器人，可以理解和生成人类语言。',
          image: 'https://via.placeholder.com/300x200?text=NLP+Chatbot'
        },
        {
          id: 2,
          title: '计算机视觉图像识别',
          description: '使用深度学习模型进行图像分类和物体检测的AI系统。',
          image: 'https://via.placeholder.com/300x200?text=CV+Image+Recognition'
        },
        {
          id: 3,
          title: 'AI辅助决策系统',
          description: '结合机器学习和专家系统的智能决策支持工具。',
          image: 'https://via.placeholder.com/300x200?text=AI+Decision+Support'
        }
      ],
      userInput: '',
      chatMessages: []
    }
  },
  methods: {
    sendMessage() {
      if (this.userInput.trim() === '') return;
      this.chatMessages.push({ sender: 'user', text: this.userInput });
      // 这里可以添加调用AI服务的逻辑
      setTimeout(() => {
        this.chatMessages.push({ sender: 'ai', text: '这是AI的回复。' });
        this.$nextTick(() => {
          this.$refs.chatMessages.scrollTop = this.$refs.chatMessages.scrollHeight;
        });
      }, 1000);
      this.userInput = '';
    }
  }
}
</script>

<style>
#app {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}

.navbar {
  transition: background-color 0.3s ease;
}

.banner {
  height: 60vh; /* 减小banner高度 */
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url('https://images.unsplash.com/photo-1620712943543-bcc4688e7485?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1920&q=80') no-repeat center center;
  background-size: cover;
  color: white;
}

.card {
  transition: transform 0.3s ease;
}

.card:hover {
  transform: translateY(-5px);
}

.chat-messages {
  height: 300px;
  overflow-y: auto;
  border: 1px solid #dee2e6;
  border-radius: 0.25rem;
  padding: 10px;
}

.message {
  margin-bottom: 10px;
  padding: 5px 10px;
  border-radius: 10px;
  max-width: 70%;
}

.user {
  background-color: #007bff;
  color: white;
  align-self: flex-end;
  margin-left: auto;
}

.ai {
  background-color: #f8f9fa;
  align-self: flex-start;
}

@media (max-width: 768px) {
  .banner {
    height: 40vh; /* 在移动设备上进一步减小高度 */
  }
}
</style>
